:root{
    --var-fundoNav:rgb(255,255,255);
    --var-fundoBtns:rgb(200,200,200);
}

#navBar{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.cima{
    display: flex;
    justify-content: space-between;
    align-items: center;

    border-radius: 30px;
    border: #000 solid 2px;
    margin-top: 10px;
    width: 90%;
    transition: border-radius 2s;

    background-color: var(--var-fundoNav);
}
.cima img{
    height: 200px;
}

.btns{
    display: flex;
    margin-right: 40px;
    gap: 20px;

}
.btns div{
    font-size: 50px;
    color: #000;
    transition: color 1s;
}
.btns div:hover{
    cursor: pointer;
    color: #0f0;
}

.btnsCel{
    width: 50px;
    height: 50px;
    margin-right: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.btnsCel:hover{
    cursor: pointer;
}

.treco{
    position: relative;
    height: 5px;
    width: 80%;
    background-color: #000;
    border-radius: 10px;
}

.options{
    display: flex;
    width: 90%;
    flex-direction: column;
    background-color: var(--var-fundoNav);
    gap: 10px;
    align-items: center;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
    border-left: #000 solid 2px;
    border-right: #000 solid 2px;
    border-bottom: #000 solid 2px;
}

.option{
    text-align: center;
    font-size: 20px;
    width: 90%;
}
.option:hover{
    cursor: pointer;
}